}

form.example input[type=text] {
    padding: 10px;
    font-size: 17px;
    border: 2px solid transparent ;
    float: left;
    width: 80%;
    background: transparent;
    color: rgb(219, 213, 213);
}

input:focus {
    background-color: transparent;
    border: 2px solid transparent;
    outline: none !important;

}

::-webkit-input-placeholder{
    color:rgb(219, 213, 213);
}

:-ms-input-placeholder{
    color:rgb(219, 213, 213);
}

::-ms-input-placeholder{
    color:rgb(219, 213, 213);
}

::placeholder{
    color:rgb(219, 213, 213);
}

  
form.example button {
    float: left;
    width: 20%;
    padding: 10px;
    background: transparent;
    color: rgb(219, 213, 213);
    font-size: 17px;
    border: 2px solid transparent;
    cursor: pointer;
    

  }
  
