.App {
  text-align: center;
  
}

.backspacepersons{
  margin: auto;
  width: 80%;
  padding: 10px;
  background-color: rgb(186, 147, 224);
  opacity:80% ;
  position: absolute;
  left: 0; 
  right: 0; 
  top: 30%;
  border-radius: 5px;
  display: flex;
  flex-wrap: wrap;
  flex-basis: 1000px;
  justify-content: center;
  margin-bottom: 70px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
